
.Auth{
    display: flex;
    align-items: center;
    justify-content: center;
    height:95vh;
    gap: 4rem;
    position: relative;
}
.Auth > div{
    display: flex;
    align-items: center;
    justify-content: center;
}
/* left Side */
.auth-left{
    gap: 2rem;
}
.auth-left >img{
    width: 3rem;
    height: 3rem;
}
.webName >h1{
    font-size: 3rem;
    background-color: red;
    /* create gradient */
    background-image: var(--buttonBg);
    background-size: 100%;
    background-repeat: repeat;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-background-clip: text;
    -moz-text-fill-color: transparent;
}
.webName>h6 {
    font-size: 0.85rem;
    font-weight: 300;
    margin-top: -1rem;
}
.auth-right {
    background-color: var(--cardColor);
    border-radius: 1rem;
    padding: 1rem;
}

.infoForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2rem;
}

.infoInput {
    border: none;
    outline: none;
    background-color: var(--inputColor);
    border-radius: 8px;
    padding: 16px;
    flex: 1;
    /*  it convert width of input total space */
}

.infoForm>div {
    display: flex;
    gap: 1rem;
    height: 2rem;
    width: 90%;
}

.infoButton {
    width: 6rem;
    height: 2.2rem;
    align-self: flex-end;
}

.authForm>div {
    width: 100%;
    align-items: center;
    justify-content: center;

}