.profileCard{
    border-radius: 1.5rem;
    display: flex;
    flex-direction: column;
    position: relative;
    gap:1rem;
    overflow-x: clip;
    background: var(--cardColor);
}
.profileImage{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
   
}
.profileImage >img:nth-of-type(1){
    width: 100%;
    border-radius: 5px;
}
.profileImage>img:nth-of-type(2) {
    width: 6rem;
    height: 6rem;
    border-radius: 50%;
    position: absolute;
    bottom: -3rem;
    box-shadow: var(--profileShadow);
}
.profileName{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 2rem;
    gap: 10px;
}
.profileName>span:nth-of-type(1) {
    font-weight: bold;
}


/* followStatus */
.followStatus{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap:0.50rem
}
.followStatus > hr {
    width: 85%;
    border: 1px solid var(--hrColor);
}
.followStatus > div {
    display: flex;
    gap: 0.5rem;
    width: 80%;
    justify-content: space-around;
    align-items: center;
}
.follow{
    display: flex;
    flex-direction: column;
    gap: 0.2rem;
    justify-content: center;
    align-items: center;
}
.follow>span:nth-of-type(1) {
    font-weight: bold;
}
.follow>span:nth-of-type(2) {
    color:var(--gray);
    font-size: 14px;
    font-weight: 500;
}
.vertical{
    height: 45px;
    border: 1px solid var(--hrColor);
}

.profileCard >span{
    font-weight: bold;
    color: orange;
    align-self: center;
    margin-bottom: 1rem;
    cursor: pointer;
}