.logoSearch{
    display: flex;
    gap:0.75rem;
    align-items: center;
}

.search{
    display: flex;
    background-color: var(--inputColor);
    border-radius: 10px;
    padding: 5px 6px;
}
.search input{
    outline: none;
    background-color: transparent;
    border: none;
    padding-right: 4rem;
}
.s-icon{
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(106.23deg,
        #f99827,
        #f95f35 100%);
    border-radius: 5px;
    padding: 4px;
    color: white;
    width: 18px;
    height: 18px;
}
.s-icon :hover{
    cursor: pointer;
}